.App {
  text-align: center;
}
.login-register {
  width: 30%;
  border: 2px solid #768084;
  color: #fff;
  padding: 10px;
  margin: 0 auto;
}
input {
  /*color: white!important;*/
  border: 2px solid #768084!important;
  background-color: #181a1b!important;
  color: #c7ced0!important;
}
textarea {
  /*color: white!important;*/
  border: 2px solid #768084!important;
  background-color: #181a1b!important;
}
.card{
  width: 25%;
  border: 2px solid #768084;
  margin: 5px 5px 20px;
  background-color: #181a1b;
  color: #c7ced0;
}
a{
  color: #c7ced0;
  text-decoration: none;
}
a:hover{
  color: #c7ced0;
  text-decoration: underline;
}
.card-header{
  background-color: #8e0000;
  height: 100%;
  color: #fff;
}
.card-footer{
  /*background-color: #8e0000!important;*/
}
button{
  margin-top: 10px;
}
img {
  width: 100%;
}
.event-card, .left-boarder{
  width: 40%;
}
.left-boarder {
  background-color: #8e0000;
  width: 50%;
}
.right-boarder {
  height: 100%;
  background-color: #181a1b;
  border-top: 4px solid #c7ced0;
  border-right: 4px solid #c7ced0;
  border-bottom: 4px solid #c7ced0;
  color: #181a1b;
}
.test{
  display: flex;
}